// @flow
// split at each occurence of splitToken, but only if no braces are currently open.
export function splitTeXString(texString
/*: string */
)
/*: Array<string> */
{
  var splitToken
  /*: string */
  = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'and';
  var output = [];
  var tokenRe = /([^\s{}]+|\s|{|})/g;
  var j = 0;
  var k = 0;
  var item;

  while ((item = tokenRe.exec(texString)) !== null) {
    var token = item && item.length ? item[0] : false;

    if (token === false) {
      break;
    }

    if (k === output.length) {
      output.push('');
    }

    switch (token) {
      case '{':
        j += 1;
        output[k] += token;
        break;

      case '}':
        j -= 1;
        output[k] += token;
        break;

      case splitToken:
        if (0 === j) {
          k++;
        } else {
          output[k] += token;
        }

        break;

      default:
        output[k] += token;
    }
  }

  return output;
}